"use client";

import { useState } from "react";
import { Download, Calendar, User, Tag, CheckCircle, AlertCircle } from "lucide-react";
import DownloadModal from "@/components/software/DownloadModal";

type Software = {
  id: string;
  slug: string;
  title: string;
  version: string;
  file?: string | null;
  external_url?: string | null;
  price?: string;
  license?: string;
  is_free?: boolean;
  one_line_description?: string;
  description?: string;
  developer?: string;
  requirements?: string[];
  platforms?: string[];
  tags?: string[];
  size?: string | null;
  released_at?: string | null;
  updated_at?: string;
  download_count?: number;
};

interface SoftwareDetailClientProps {
  software: Software | null;
  error: string | null;
}

export default function SoftwareDetailClient({ software, error }: SoftwareDetailClientProps) {
  const [showDownloadModal, setShowDownloadModal] = useState(false);

  if (error || !software) {
    return (
      <div className="bg-white min-h-screen">
        <div className="max-w-4xl mx-auto py-12 px-4">
          <div className="text-center py-20">
            <AlertCircle className="w-16 h-16 text-red-500 mx-auto mb-4" />
            <h1 className="text-2xl font-bold text-gray-900 mb-2">Software Not Found</h1>
            <p className="text-gray-600">{error || "The requested software could not be found."}</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white min-h-screen">
      <div className="max-w-4xl mx-auto py-8 px-4">
        {/* Header Section */}
        <div className="bg-gradient-to-r from-blue-50 to-indigo-50 rounded-2xl p-8 mb-8 border border-blue-100">
          <div className="flex flex-col lg:flex-row justify-between items-start lg:items-center gap-6">
            <div className="flex-1">
              <div className="flex items-center gap-3 mb-3">
                <h1 className="text-3xl lg:text-4xl font-bold text-gray-900">
                  {software.title}
                </h1>
                {software.is_free ? (
                  <span className="bg-green-100 text-green-700 px-3 py-1 rounded-full text-sm font-semibold">
                    Free
                  </span>
                ) : (
                  <span className="bg-yellow-100 text-yellow-700 px-3 py-1 rounded-full text-sm font-semibold">
                    Paid
                  </span>
                )}
              </div>
              
              <p className="text-xl text-gray-600 mb-4">
                {software.one_line_description}
              </p>
              
              <div className="flex flex-wrap gap-4 text-sm text-gray-500">
                <div className="flex items-center gap-1">
                  <Tag className="w-4 h-4" />
                  Version {software.version}
                </div>
                {software.license && (
                  <div className="flex items-center gap-1">
                    <User className="w-4 h-4" />
                    License: {software.license}
                  </div>
                )}
                {software.updated_at && (
                  <div className="flex items-center gap-1">
                    <Calendar className="w-4 h-4" />
                    Updated: {new Date(software.updated_at).toLocaleDateString()}
                  </div>
                )}
              </div>
            </div>
            
            <button
              onClick={() => setShowDownloadModal(true)}
              className="bg-orange-500 hover:bg-orange-600 text-white px-8 py-3 rounded-xl font-semibold flex items-center gap-2 transition-colors shadow-lg hover:shadow-xl"
            >
              <Download className="w-5 h-5" />
              Download Now
            </button>
          </div>
        </div>

        {/* Main Content */}
        <div className="grid lg:grid-cols-3 gap-8">
          {/* Left Column - Description */}
          <div className="lg:col-span-2">
            <div className="bg-white border border-gray-200 rounded-2xl p-6 mb-6">
              <h2 className="text-2xl font-bold text-gray-900 mb-4">Description</h2>
              <div className="prose prose-gray max-w-none">
                <p className="text-gray-700 leading-relaxed">
                  {software.description}
                </p>
              </div>
            </div>

            {/* Features */}
            {software.tags && software.tags.length > 0 && (
              <div className="bg-white border border-gray-200 rounded-2xl p-6">
                <h2 className="text-2xl font-bold text-gray-900 mb-4">Features</h2>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
                  {software.tags.map((tag, index) => (
                    <div key={index} className="flex items-center gap-2 text-gray-700">
                      <CheckCircle className="w-4 h-4 text-green-500" />
                      {tag}
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>

          {/* Right Column - Details */}
          <div className="space-y-6">
            {/* System Requirements */}
            {software.requirements && software.requirements.length > 0 && (
              <div className="bg-white border border-gray-200 rounded-2xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-3">Requirements</h3>
                <ul className="space-y-2">
                  {software.requirements.map((req, index) => (
                    <li key={index} className="flex items-center gap-2 text-sm text-gray-700">
                      <div className="w-1.5 h-1.5 bg-blue-500 rounded-full"></div>
                      {req}
                    </li>
                  ))}
                </ul>
              </div>
            )}

            {/* Supported Platforms */}
            {software.platforms && software.platforms.length > 0 && (
              <div className="bg-white border border-gray-200 rounded-2xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-3">Supported Platforms</h3>
                <ul className="space-y-2">
                  {software.platforms.map((platform, index) => (
                    <li key={index} className="flex items-center gap-2 text-sm text-gray-700">
                      <div className="w-1.5 h-1.5 bg-green-500 rounded-full"></div>
                      {platform}
                    </li>
                  ))}
                </ul>
              </div>
            )}

            {/* Developer Info */}
            {software.developer && (
              <div className="bg-white border border-gray-200 rounded-2xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-3">Developer</h3>
                <p className="text-gray-700">{software.developer}</p>
              </div>
            )}

            {/* Download Stats */}
            {software.download_count !== undefined && (
              <div className="bg-blue-50 border border-blue-200 rounded-2xl p-6">
                <h3 className="text-lg font-semibold text-gray-900 mb-2">Download Statistics</h3>
                <p className="text-2xl font-bold text-blue-600">{software.download_count.toLocaleString()}</p>
                <p className="text-sm text-gray-600">Total Downloads</p>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Download Modal */}
      {showDownloadModal && (
        <DownloadModal
          software={{
            id: software.id,
            title: software.title,
            version: software.version,
            file: software.file,
            external_url: software.external_url,
          }}
          onClose={() => setShowDownloadModal(false)}
        />
      )}
    </div>
  );
}